<template>
  <div>
    <!--  >  >   -->
    <div class="queryConditionBox clearFloat">
      <div>
        <div class="queryConditionGroup clearFloat">
          <div class="queryCondition">
            <!-- <span></span>
            <input v-model="queryCondition.start_date" type="text">
            <span></span>
            <input v-model="queryCondition.end_date" type="text"> -->
            <div class="floatLeft">
              <date-picker :prop="startDatePicker" @startDateEvent="startDateEvent"></date-picker>
            </div>
            <div class="floatLeft">
              <date-picker :prop="endDatePicker" @endDateEvent="endDateEvent"></date-picker>
            </div>
          </div>
          <div class="queryCondition">
            <!-- <span></span>
            <input v-model="queryCondition.changeDate" type="text"> -->
            <date-picker :prop="changeDatePicker" @changeDateEvent="changeDateEvent"></date-picker>
          </div>
        </div>
        <div class="queryConditionGroup clearFloat">
          <div class="queryCondition"> 
            <pull-down-list :prop="changeDirection"  @changeEvent = "changeEvent"></pull-down-list>
          </div>
          <div class="queryCondition">
            <pull-down-list :prop="ratingNameData" @ratingName = "ratingName"></pull-down-list>
          </div>
        </div>
      </div>
      <div class="queryBtn">
        <span @click="query"></span>
      </div>
    </div>
    <div v-if="isQueryResult">
      <!-- <rating-change :prop="ratingChangeData"></rating-change> -->
      <div class="queryResult">
        <table>
          <tbody>
            <tr>
              <th v-for="(item, index) of tableData.th" :key="index" class="title">{{item}}</th>
            </tr>
            <tr v-for="(item, index) of tableData.tr" :key="index">
              <td>{{index + 1}}</td>
              <td>{{item.notice_date}}</td>
              <td>{{item.changedate}}</td>
              <td>{{item.creditname}}</td>
              <td>{{item.creditcode}}</td>
              <td @click="portfoliocodeDetails(item)" class="portfoliocode">{{item.portfoliocode}}</td>
              <td>{{item.ratefwd}}</td>
              <td>{{item.rating}}</td>
              <td>{{item.ratingstyle}}</td>
              <td>{{item.ratingtype}}</td>
              <td>{{item.changeway}}</td>
              <td>{{item.reason}}</td>
            </tr>
          </tbody>
        </table>
        <pagination :prop="paginationData" @paginationSelect="paginationSelect"></pagination>
      </div>
    </div>
    <div v-if="isShowDetails" class="detailsPopUp">
      <div class="detailsBox">
        <div class="details">
          <ul>
            <li>{{detailsData.securitysname}}</li>
            <li>{{detailsData.securityname}}</li>
            <li>{{detailsData.securitycode}}</li>
            <li>{{detailsData.securitytype}}</li>
            <li>{{detailsData.issuetype}}</li>
            <li>{{detailsData.portfoliocode}}</li>
          </ul>
        </div>
        <span @click="closeDetails" class="closeBtn"></span>
      </div>
    </div>
  </div>
</template>

<script>
import ratingChange from '@/components/common/ratingChangeList'
import pullDownList from '@/components/common/pullDownList'
import pagination from '@/components/common/pagination'
import datePicker from '@/components/common/datePicker'
export default {
  data(){
    return {
      url: 'http://10.25.24.51:10193/api/risk/bond_rate_change?',
      isQueryResult:false,
      isShowDetails: false,
      ratingChangeData: [],
      queryCondition:{
        start_date: '',
        end_date: '',
        changedate: '',
        changeway: '',
        creditname: '',
        page: 0,
        pagesize: 10
      },
      sendData:{},
      detailsData: {},
      tableData:{
        th:['', '', '', '', '', '', '', '', '', '', '', ''],
        tr: [
          {notice_date: '', changedate: '', creditcode: '', creditname: '', portfoliocode: '', ratefwd: '', rating: '', ratingstyle: '', ratingtype: '', changeway:'', reason: ''}
          //  changedate changeway creditcode creditname notice_date portfoliocode ratefwd rating ratingstyle ratingtype reason
        ]
      },
      paginationData:{
        parentEvent: 'paginationSelect',
        page_Count: 1,
        total_Count: 1,
        current: 1
      },
      changeDatePicker:{
        title: '',
        parentEvent: 'changeDateEvent'
      },
      startDatePicker:{
        title: '',
        parentEvent: 'startDateEvent'
      },
      endDatePicker:{
        title: '',
        parentEvent: 'endDateEvent'
      },
      ratingNameData: {title: '', parentEvent: 'ratingName', default: '', listWidth: 230, nowSelectWidth: 150, list: [ '', '', '', '', '', '', '', '']},
      changeDirection: {title: '', parentEvent: 'changeEvent', default: '', listWidth: 150, nowSelectWidth: 150, list: [ '', '', '', '', '']},
    }
  },
  components:{
    ratingChange,
    pullDownList,
    pagination,
    datePicker
  },
  methods:{
    query(){
      this.isQueryResult = false;
      this.sendData = JSON.parse(JSON.stringify(this.queryCondition));
      // let url = this.url 
      //   // + 'start_date=&end_date=&changedate=&creditname=&changeway=03&page=0&pagesize=10';
      //   + 'start_date=' + this.sendData.start_date + '&'
      //   + 'end_date=' + this.sendData.end_date + '&'
      //   + 'changedate=' + this.sendData.changedate + '&'
      //   + 'creditname=' + this.sendData.creditname + '&'
      //   + 'changeway=' + this.sendData.changeway + '&'
      //   + 'page=' + this.sendData.page + '&'
      //   + 'pagesize=' + this.sendData.pagesize;
      for(let key in this.sendData){
        if(this.sendData[key] === ''){
          delete this.sendData[key];
        }
      }
      console.log('sendData',this.sendData)
      this.$_axios.get(this.url,{
        params:this.sendData
      }).then(response => {
        const resultData = response.data.result;
        console.log(JSON.stringify(response.data))
        this.isQueryResult = true;
        this.tableData.tr = JSON.parse(JSON.stringify(resultData.result));
        this.paginationData.total_Count = resultData.total_count;
        this.paginationData.page_Count = Math.ceil(resultData.total_count / 10);
        console.log(' >  >  ',resultData);
      })
      .catch(err =>{
        console.log('');
      });
    },
    paginationSelect(pageNumber){
      const sendData = JSON.parse(JSON.stringify(this.sendData));
      // let url = this.url 
      //   // + 'start_date=&end_date=&changedate=&creditname=&changeway=03&page='+ (pageNumber - 1) +'&pagesize=10';
      //   + 'start_date=' + sendData.start_date + '&'
      //   + 'end_date=' + sendData.end_date + '&'
      //   + 'changedate=' + sendData.changedate + '&'
      //   + 'creditname=' + sendData.creditname + '&'
      //   + 'changeway=' + sendData.changeway + '&'
      //   + 'page=' + (pageNumber - 1) + '&'
      //   + 'pagesize=' + sendData.pagesize;
      sendData.page = pageNumber - 1;
      console.log('sendData',sendData)
      this.$_axios.get(this.url,{
        params:sendData
      }).then(response => {
        const resultData = response.data.result;
        this.isQueryResult = true;
        this.tableData.tr = JSON.parse(JSON.stringify(resultData.result));
        console.log(resultData.total_count)
        this.paginationData.total_Count = resultData.total_count;
        this.paginationData.page_Count = Math.ceil(resultData.total_count / 10);
        console.log(' >  >  ',resultData);
      })
      .catch(err =>{
        console.log('');
      });
    },
    ratingName(...data){
      console.log('allData:',data)
      if(data[0] === ''){
        this.queryCondition.creditname = '';
      } else {
        this.queryCondition.creditname = data[0];
      }
    },
    changeEvent(...data){
      console.log('allData:',data)
      // '', '', '', '', ''
      switch (data[0]) {
        case '':
          this.queryCondition.changeway = '03';
          break;
        case '':
          this.queryCondition.changeway = '01';
          break;
        case '':
          this.queryCondition.changeway = '02';
          break;
        case '':
          this.queryCondition.changeway = '04';
          break;
        case '':
          this.queryCondition.changeway = '05';
          break;
        default:
          this.queryCondition.changeway = '';
          break;
      }
    },
    portfoliocodeDetails(item){
      this.isShowDetails = false;
      let url = 'http://10.25.24.51:10193/api/risk/bond_base_detail?'
        + 'portfoliocode=' + item.portfoliocode;
      this.$_axios.get(url)
      .then(response => {
        this.isShowDetails = true;
        const resultData = response.data.result;
        this.detailsData = JSON.parse(JSON.stringify(resultData.result));
        console.log(resultData)

      })
      .catch(err => {
        console.log(err);
      });
    },
    closeDetails(){
      this.isShowDetails = false;
      this.detailsData = {};
    },
    startDateEvent(...data){
      this.queryCondition.start_date = data[0];
    },
    endDateEvent(...data){
      this.queryCondition.end_date = data[0];
    },
    changeDateEvent(...data){
      this.queryCondition.changedate = data[0];
    },
  }
}
</script>

<style lang="less" scoped>
.queryConditionBox{
  position: relative;
  width: 1180px;
  height: 180px;
  // background-color: rgba(32,192,252, 0.1);
  border-bottom: 1px solid #797979;
  font: 400 13px/20px Arial;
  input{
    width: 110px;
    height: 25px;
    line-height: 25px;
  }
  .queryConditionGroup{
    width: 780px;
    margin-top: 20px;
  }
  .queryCondition{
    float: left;
    position: relative;
    margin-left: 20px;
    margin-top: 10px;
    input.break{
      position: relative;
      top: -15px; 
    }
  }
  .modelTitle{
    display: inline-block;
    max-width: 75px;
    height: 40px;
    text-align: left;
    line-height: 20px;
  }
}
.queryBtn{
  position: absolute;
  top: 65px;
  right: 400px;
  span{
    display: inline-block;
    width: 60px;
    height: 30px;
    text-align: center;
    font: 400 16px/30px Arial;
    cursor: pointer;
    border: 1px solid #797979;
    border-radius: 5px;
  }
}
.queryResult{
  table{
    width: 100%;
    border-collapse: collapse;
    border-top: 2px solid #a9a9a9;
    background: #FFFFFF;
  }
  tbody{
    display: table-row-group;
    vertical-align: middle;
    border-color: inherit;
    th{
      border: 1px solid #797979;
      height: 40px;
      line-height: 40px;
      background-color: #f0f5f9;
    }
    td{
      border: 1px solid #797979;
      text-align: center;
      vertical-align: middle;
      padding-top: 30px;
      padding-bottom: 20px;
    }
    .portfoliocode{
      cursor: pointer;
      text-decoration: underline;
    }
    .title:nth-child(1){
      width: 35px;
    }
    .title:nth-child(2){
      width: 80px;
    }
    .title:nth-child(3){
      width: 80px;
    }
    .title:nth-child(4){
      width: 90px;
    }
    .title:nth-child(5){
      width: 70px;
    }
    .title:nth-child(6){
      width: 70px;
    }
    .title:nth-child(7){
      width: 70px;
    }
    .title:nth-child(8){
      width: 70px;
    }
    .title:nth-child(9){
      width: 80px;
    }
    .title:nth-child(10){
      width: 95px;
    }
    .title:nth-child(11){
      width: 60px;
    }
  }
}
.detailsPopUp{
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  border: 1px solid #797979;
  word-break:break-all;
  word-wrap:break-word;
  background-color: #fff;
  .detailsBox{
    position: relative;
    width: 630px;
    margin-left: auto;
    margin-right: auto;
    .details{
      position: absolute;
      top: 100px;
      right: 0;
      bottom: 0;
      left: 100px;
      margin: auto;
    }
    .closeBtn{
      position: absolute;
      right: -50px;
      top: 30px;
      font-size: 40px;
      cursor: pointer;
    }
  }
}
</style>



