import Vue from 'vue'
import Router from 'vue-router'
// 
import homePage from '@/components/homePage/homePage'
// 
import riskQuery from '@/components/riskQuery/riskQuery'
// 
import companyWarning from '@/components/companyWarning/companyWarning'
// 
import subMonitoring from '@/components/subMonitoring/subMonitoring'
// 
import blackSwan from '@/components/blackSwan/blackSwan'


// >>>
import homeContent from '@/components/homePage/homeContent'

// >> 
//  > 
import bond_negativeNews from '@/components/homePage/bond/negativeNews'
//  > 
import bond_newsFavorite from '@/components/homePage/bond/newsFavorite'
//  > 
import bond_issuerNotice from '@/components/homePage/bond/issuerNotice'
//  >  
import bond_manageNotice from '@/components/homePage/bond/manageNotice'
//  >  
import bond_ratingInformation from '@/components/homePage/bond/ratingInformation'
//  >  
import bond_ratingChange from '@/components/homePage/bond/ratingChange'
// >> 

// >> 
//  > 
import fund_shigekuraNews from '@/components/homePage/fund/shigekuraNews'
//  > 
import fund_managerNews from '@/components/homePage/fund/managerNews'
//  > 
import fund_newsFavorite from '@/components/homePage/fund/newsFavorite'
// 
import fund_issuerNotice from '@/components/homePage/fund/issuerNotice'
// 
import fund_info from '@/components/homePage/fund/fund_info'
// >> 

// >> 
// 
import stock_index_futures from '@/components/homePage/futureGoods/stock_index_futures'
// >> 

// >> 
//  > 
import NEEQ_NewsMonitoring from '@/components/homePage/NEEQ/newsMonitoring'
//  > 
import NEEQ_favorite from '@/components/homePage/NEEQ/favorite'
//  > 
import NEEQ_announcement from '@/components/homePage/NEEQ/announcement'
//  > 
import NEEQ_dynamic from '@/components/homePage/NEEQ/dynamic'
//  > 
import NEEQ_selfMeasures from '@/components/homePage/NEEQ/selfMeasures'
//  > 
import NEEQ_punishment from '@/components/homePage/NEEQ/punishment'
//  > 
import NEEQ_inquiryLetter from '@/components/homePage/NEEQ/inquiryLetter'
// >> 

// >> 
// 
import shareOption_page1 from '@/components/homePage/shareOption/shareOption_page1'
// 
import shareOption_page2 from '@/components/homePage/shareOption/shareOption_page2'
// >> 

// >> 
// 
import stock_newsWarning from '@/components/homePage/stock/newsWarning'
// 
import stock_afficheWarning from '@/components/homePage/stock/afficheWarning'
// 
import stock_financeWarning from '@/components/homePage/stock/financeWarning'
// 
import stock_sharePriceWarning from '@/components/homePage/stock/sharePriceWarning'
// >> 

// >> 
//  > 
import supervise_page1 from '@/components/homePage/supervise/supervise_page1'
//  > 
import supervise_page2 from '@/components/homePage/supervise/supervise_page2'
//  > 
import supervise_page3 from '@/components/homePage/supervise/supervise_page3'
// >> 
// 
import legislation from '@/components/homePage/legislation/legislation'
// >>>

// >>>

import delistingWarning from '@/components/blackSwan/delistingWarning'
import executives from '@/components/blackSwan/executives'
import importantEvent from '@/components/blackSwan/importantEvent'

// >>>

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      redirect: '/homePage/homeContent',
      component: homeContent
    },
    {
      path: '/homePage',
      name: 'homePage',
      component: homePage,
      children: [
        {
          path: '/homePage/homeContent',
          component: homeContent
        },
        // >> 
        {
          path: '/homePage/fund_shigekuraNews',
          component: fund_shigekuraNews
        },
        {
          path: '/homePage/fund_managerNews',
          component: fund_managerNews
        },
        {
          path: '/homePage/fund_newsFavorite',
          component: fund_newsFavorite
        },
        {
          path: '/homePage/fund_issuerNotice',
          component: fund_issuerNotice
        },
        // 
        {
          path: '/homePage/fund_info',
          component: fund_info
        },
        // >> 

        //  >> 
        {
          path: '/homePage/bond_negativeNews',
          component: bond_negativeNews
        },
        {
          path: '/homePage/bond_newsFavorite',
          component: bond_newsFavorite
        },
        {
          path: '/homePage/bond_issuerNotice',
          component: bond_issuerNotice
        },
        {
          path: '/homePage/bond_manageNotice',
          component: bond_manageNotice
        },
        {
          path: '/homePage/bond_ratingInformation',
          component: bond_ratingInformation
        },
        {
          path: '/homePage/bond_ratingChange',
          component: bond_ratingChange
        },
        //  >> 

        // >> 
        {
          path: '/homePage/stock_index_futures',
          component: stock_index_futures
        },
        // >> 

        // >> 
        {
          path: '/homePage/legislation',
          component: legislation
        },
        // >> 
        
        // >> 
        {
          path: '/homePage/NEEQ_NewsMonitoring',
          component: NEEQ_NewsMonitoring
        },
        {
          path: '/homePage/NEEQ_favorite',
          component: NEEQ_favorite
        },
        {
          path: '/homePage/NEEQ_announcement',
          component: NEEQ_announcement
        },
        {
          path: '/homePage/NEEQ_dynamic',
          component: NEEQ_dynamic
        },
        {
          path: '/homePage/NEEQ_punishment',
          component: NEEQ_punishment
        },
        {
          path: '/homePage/NEEQ_selfMeasures',
          component: NEEQ_selfMeasures
        },
        {
          path: '/homePage/NEEQ_inquiryLetter',
          component: NEEQ_inquiryLetter
        },
        // >> 

        // >> 
        {
          path: '/homePage/shareOption_page1',
          component: shareOption_page1
        },
        {
          path: '/homePage/shareOption_page2',
          component: shareOption_page2
        },
        // >> 

        // >> 
        {
          path: '/homePage/stock_newsWarning',
          component: stock_newsWarning
        },
        {
          path: '/homePage/stock_afficheWarning',
          component: stock_afficheWarning
        },
        {
          path: '/homePage/stock_financeWarning',
          component: stock_financeWarning
        },
        {
          path: '/homePage/stock_sharePriceWarning',
          component: stock_sharePriceWarning
        },
        // >> 

        // >> 
        {
          path: '/homePage/supervise_page1',
          component: supervise_page1
        },
        {
          path: '/homePage/supervise_page2',
          component: supervise_page2
        },
        {
          path: '/homePage/supervise_page3',
          component: supervise_page3
        },
        // >> 
      ]
    },
    {
      path: '/riskQuery',
      name: 'riskQuery',
      component: riskQuery
    },
    {
      path: '/companyWarning',
      name: 'companyWarning',
      component: companyWarning
    },
    {
      path: '/subMonitoring',
      name: 'subMonitoring',
      component: subMonitoring
    },
    {
      path: '/blackSwan',
      name: 'blackSwan',
      component: blackSwan,
      children: [
        {
          path: '/blackSwan/delistingWarning',
          component: delistingWarning
        },
        {
          path: '/blackSwan/executives',
          component: executives
        },
        {
          path: '/blackSwan/importantEvent',
          component: importantEvent
        },
      ]
    }
  ]
})



