import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    isShowPopUp: false,
    routerGoNow: true,
    popUpData: {
      type: '',
      fund_info: null,
      fundManagerData: null,
      purRedTypeData: null,
      purRedInfoData: null,
      popUpDataLength: 1,
      translateData: 0,
      nowIndex: 0,
      isShowPreviousBtn: false,
      isShowNextBtn: false
    }
  },
  
})


