<template>
  <div id="app">
    <!--  opinion monitor -->
    <h1><span></span></h1>
    <div class="homePageNav clearFloat">
      <div class="link-homePage">
        <router-link to="/homePage/homeContent"></router-link>
      </div>
      <div class="link-riskQuery">
        <router-link to="/riskQuery"></router-link>
      </div>
      <div class="link-companyWarning">
        <router-link to="/companyWarning"></router-link>
      </div>
      <div class="link-subsidiryMonitoring">
        <router-link to="/subMonitoring"></router-link>
      </div>
      <div class="link-blackSwan">
        <router-link to="/blackSwan/delistingWarning"></router-link>
      </div>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
import homePage from '@/components/homePage/homePage';

export default {
  name: 'App',
  data(){
    return {
      isShowPopUp: false
    }
  },
  components:{
    homePage,
  }
}
</script>

<style lang="less" scoped>
#app{
  margin-bottom: 50px;
}
h1{
    display: block;
    width: 243px;
    height: 49px;
    font-size: 28px;
    line-height: 49px;
    font-style: oblique;
    margin-top: 20px;
    margin-left: 50px;
    padding-left: 10px;
    color: #000000;
    background-color: #99FFFF;
    span{
        color: #FF6633
    }
}
.homePageNav{
    width: 1452px;
    height: 37px;
    font: 14px/37px Arial;
    color: #FFFFFF;
    margin-top: 10px;
    margin-left: 50px;
    padding-left: 265px;
    background-color: #CC0000;
    border: 1px solid #797979;
    overflow: hidden;
    a{
        display: inline-block;
        width: 100%;
        height: 100%;
        color: #FFFFFF;
        text-decoration:none;
    }
    a.router-link-active{
      background-color: #6acdfd;
    }
    >div{
        float: left;
        width: 129px;
        text-align: center;
        cursor: pointer;
        border-left: 1px solid #797979;
    }
    >div:hover{
        background-color: #6acdfd;
    }
    
    .link-homePage{
        width: 140px;
    }
    .link-blackSwan{
        border-right: 1px solid #797979;
    }
}
</style>



