<template>
  <div class="sidebarContent">
    <div class="queryCondition">
      <span></span>
      <input type="text">
      <span></span>
      <input type="text">
      <span class="significanceBefore"></span>
      <input class="significance" type="text" placeholder="">
      <span class="queryConditionBtn"></span>
      <br>
      <span class="riskTypeBefore"></span>
      <!--  -->
      <span class="riskTypeDown" @click="showRiskTypeList">
        {{nowRiskType}}
        <i class="iconfont icon-triangle-bottom"></i>
        <span v-show="isShowRiskTypeList" class="riskTypeDownBox">
          <span v-for="(item,index) of riskTypeList" 
            :key="index" 
            @click="checkRiskType(item)" class="riskType">
            {{item}}
          </span>
        </span>
      </span>
    </div>
      <div class="featureBox clearFloat">
        <div class="uploadFiles">(csvexcel)</div>
        <div class="feature clearFloat">
          <div></div>
          <div></div>
        </div>
      </div>
      <div v-if="isShowCustomType" class="customTypeListBox">
        <div class="customTypeListFirst">
          <ul class="clearFloat">
            <li class="allType firstType">
              <span @click="checkAllType" class="typeIconBox"><i v-show="isCheckAll" class="iconfont icon-queren"></i></span>
              <span></span>
            </li>
            <br>
            <li class="firstType" v-for="(item,index) of customType" 
              :key="index" >
              <span @click="checkType(item)" class="typeIconBox"><i v-show="item.isCheck" class="iconfont icon-queren"></i></span>
              <span>{{item.title}}</span>
            </li>
          </ul>
        </div>
        <div class="CustomTypeListSecond"></div>
        <div></div>
    </div>
  </div>
</template>

<script>
export default {
    data(){
        return {
            // 
            isShowRiskTypeList: false,
            isShowCustomType: false,
            isCheckAll: false,
            nowRiskType: '',
            riskTypeList: ['', '', ''],
            customType: [ 
                {title: '', isCheck: false},
                {title: '', isCheck: false},
                {title: '', isCheck: false},
                {title: '', isCheck: false},
                {title: '', isCheck: false},
                {title: '', isCheck: false},
                {title: '', isCheck: false},
                {title: '', isCheck: false},
                {title: '', isCheck: false},
                {title: '', isCheck: false},
                {title: '', isCheck: false}
            ]
        }
    },
    methods:{
        // 
        showRiskTypeList(){
            this.isShowRiskTypeList = !this.isShowRiskTypeList;
        },
        // 
        checkRiskType(item){
            console.log(item)
            this.nowRiskType = item;
            if(item === ''){
                this.isShowCustomType = true;
            } else {
                this.isShowCustomType = false;
            }
        },
        // 
        checkAllType(){
            this.isCheckAll = !this.isCheckAll;
            this.customType.forEach(item => {
                item.isCheck = this.isCheckAll;
            });
        },
        checkType(item){
            item.isCheck = !item.isCheck;
            this.isCheckAll = false;
        }
    }
}
</script>

<style lang="less" scoped>
.customTypeListFirst{
    width: 800px;
    margin-left: 230px;
    margin-top: 50px;
    font-size: 12px;
    .firstType{
        float: left;
        margin-left: 40px;
    }
    .typeIconBox{
        position: relative;
        display: inline-block;
        width: 11px;
        height: 11px;
        background-color: #fff;
        border: 1px solid #797979;
        border-radius: 2px;
        margin-right: 20px;
        .icon-queren{
            position: absolute;
            top: -2px;
            left: -7px;
        }
    }
}
.sidebarContent{
    float: left;
}
.queryCondition{
  margin-top: 100px;
  margin-left: 70px;
  padding-left: 100px;
  padding-right: 150px;
  input{
    width: 120px;
    height: 26px;
  }
  .significanceBefore{
    display: inline-block;
    margin-left: 10px;
  }
  .riskTypeDown{
    position: relative;
    width: 144px;
    height: 22px;
    line-height: 22px;
    cursor: pointer;
    display: inline-block;
    border: 1px solid #797979;
    .riskTypeDownBox{
      position: absolute;
      top: 22px;
      left: 0px;
      display: inline-block;
      width: 144px;
      border: 1px solid #797979;
    }
    .riskType{
      display: inline-block;
      width: 144px;
      cursor: pointer;
      background: #FFFFFF;
    }
    .riskType:hover{
      background-color: #6acdfd;
    }
    .icon-triangle-bottom{
      position: absolute;
      top: 0px;
      right: 3px;
    }
  }
  .riskTypeBefore{
    display: inline-block;
    margin-top: 20px;
    margin-bottom: 20px;
  }
  .queryConditionBtn{
    display: inline-block;
    width: 70px;
    height: 30px;
    text-align: center;
    line-height: 30px;
    margin-left: 50px;
    border: 1px solid rgb(121,121,121);
    border-radius: 8px;
    cursor: pointer;
  }
}
.featureBox{
  padding-left: 100px;
  margin-left: 70px;
  padding-bottom: 20px;
  border-bottom: 1px solid #797979;
  .uploadFiles{
    float: left;
  }
.feature{
    float: right;
    >div{
      float: left;
      margin-left: 20px;
    }
  }
}
</style>



