<template>
  <div class="fmanager_infoBox">
    <ul>
      <li v-for="(item, index) of fmanager_info" :key="index" class="fmanager_info">
        <div> {{item.MANAGER}}</div>
        <div> {{item.POSTSTATE}}</div>
        <div> {{item.POSTSDATE.toLocaleString()}}</div>
        <div> {{item.POSTEDATE.toLocaleString()}}</div>
        <div> {{item.YIELDSINCES.toFixed(2)}}%</div>
        <div> {{item.UPDATEDATE.toLocaleString()}}</div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: ['prop'],
  computed:{
    fmanager_info(){
      // const fmanagerInfo = this.prop.fmanager_info;
      console.log('',this.prop)
      if(this.prop){
        const fmanagerInfo = this.prop.fmanager_info ? this.prop.fmanager_info : [{}];
        fmanagerInfo.forEach(item => {
          item.POSTSDATE = item.POSTSDATE ? new Date(item.POSTSDATE) : '-';
          item.POSTEDATE = item.POSTEDATE ? new Date(item.POSTEDATE) : '-';
          item.UPDATEDATE = item.UPDATEDATE ? new Date(item.UPDATEDATE) : '-';
          // -10
          item.POSTSTATE = item.POSTSTATE == -1 ? '' : '';
        });
        return fmanagerInfo;
      }
    }
  },
}
</script>

<style lang="less" scoped>
.fmanager_infoBox{
  margin: 70px 0 0 100px;
  .fmanager_info{
    margin-top: 50px;
  }
}
</style>



