<template>
  <div>
    <!--  -->
    <div class="pur_red_type_box">
      <ul>
        <li v-for="(item, index) of purRedInfoData" :key="index" class="pur_red_type">
          <div> {{item.REPORTDATE.toLocaleString()}}</div>
          <div>() {{item.SHAREBEGIN}}</div>
          <div>() {{item.SHAREAPPLY}}</div>
          <div>() {{item.ADDFORSPLIT}}</div>
          <div>() {{item.SHAREREDEEM}}</div>
          <div>() {{item.SHAREEND}}</div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  props: ['prop'],
  computed:{
    purRedInfoData(){
      if(this.prop){
        // const purRedInfo = this.prop.purRedInfo ? this.prop.purRedInfo : [{}];
         const purRedInfo = JSON.parse(JSON.stringify(this.prop.pur_red_info));
        purRedInfo.forEach(item => {
          item.REPORTDATE = item.REPORTDATE ? new Date(item.REPORTDATE) : '-';
        });
        return purRedInfo;
      }
    },
  }
}
</script>

<style lang="less" scoped>
.pur_red_type_box{
  margin: 70px 0 0 100px;
  .pur_red_type{
    margin-top: 50px;
  }
}
</style>



