<template>
    <div class="list">
        <template v-if="count">
            <ul>
                <li v-for="(item,index) in items" :key="index">...</li>
            </ul>
            <mo-paging 
              :page-index="currentPage" 
              :totla="count" 
              :page-size="pageSize" 
              @change="pageChange">
            </mo-paging>
        </template>
    </div>
</template>
<script>
    import MoPaging from '@/components/common/paging'
    export default {
        //
        components : {
            MoPaging 
        },
        data () {
            return {
                pageSize : 20 , //20
                currentPage : 1, //
                count : 0, //
                items : [1,1,1,1]
            }
        },
        methods : {
            //
            getList () {
                //
                // let url = `/api/list/?pageSize=${this.pageSize}&currentPage=${this.currentPage}`
                // this.$http.get(url)
                // .then(({body}) => {

                //     //countDOM
                //     this.count = body.count
                //     this.items = body.list
                // })
                console.log(0)
            },

            //pagepage
            pageChange (page) {
                this.currentPage = page
                this.getList()
            }
        },
        mounted() {
            //
            this.getList()
        } 
    }
</script>


