<template>
  <div>
    <!--  -->
    <div class="pur_red_type_box">
      <!-- <div> {{purRedTypeData.SECURITYCODE}}</div>
      <div> {{purRedTypeData.SECURITYSNAME}}</div> -->
      <div> {{purRedTypeData.OFORCB}}</div>
      <div> {{purRedTypeData.ENDDATE}}</div>
      <div> {{purRedTypeData.PURTYPE}}</div>
      <div> {{purRedTypeData.REDTYPE}}</div>
      <div> {{purRedTypeData.NVPER}}</div>
      <div> {{purRedTypeData.ANVPER}}</div>
      <div> {{purRedTypeData.UPDATEDATE}}</div>
      <div> {{purRedTypeData.ASDATEOTC}}</div>
      <div> {{purRedTypeData.RSDATEOTC}}</div>
      <div> {{purRedTypeData.ASDATE}}</div>
      <div> {{purRedTypeData.RSDATE}}</div>
      <div> {{purRedTypeData.APPLYADATE}}</div>
      <div> {{purRedTypeData.REDEEMADATE}}</div>
      <div> {{purRedTypeData.RSUMPAYDATE}}</div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['prop'],
  computed:{
    purRedTypeData(){
      const popUpData = this.prop;
      const pur_or_red = {};
      if(popUpData){
        pur_or_red.ENDDATE = popUpData.pur_or_red1[0].ENDDATE ? new Date(popUpData.pur_or_red1[0].ENDDATE).toLocaleString() : '-';
        pur_or_red.PURTYPE = popUpData.pur_or_red1[0].PURTYPE;
        pur_or_red.REDTYPE = popUpData.pur_or_red1[0].REDTYPE;
        pur_or_red.NVPER = popUpData.pur_or_red1[0].NVPER;
        pur_or_red.ANVPER = popUpData.pur_or_red1[0].ANVPER;
        pur_or_red.UPDATEDATE = popUpData.pur_or_red1[0].UPDATEDATE ? new Date(popUpData.pur_or_red1[0].UPDATEDATE).toLocaleString() : '-';
        pur_or_red.ASDATEOTC = popUpData.pur_or_red2[0].ASDATEOTC ? new Date(popUpData.pur_or_red2[0].ASDATEOTC).toLocaleString() : '-';
        pur_or_red.RSDATEOTC = popUpData.pur_or_red2[0].RSDATEOTC ? new Date(popUpData.pur_or_red2[0].RSDATEOTC).toLocaleString() : '-';
        pur_or_red.ASDATE = popUpData.pur_or_red2[0].ASDATE ? new Date(popUpData.pur_or_red2[0].ASDATE).toLocaleString() : '-';
        pur_or_red.RSDATE = popUpData.pur_or_red2[0].RSDATE ? new Date(popUpData.pur_or_red2[0].RSDATE).toLocaleString() : '-';
        pur_or_red.APPLYADATE = popUpData.pur_or_red2[0].APPLYADATE ? new Date(popUpData.pur_or_red2[0].APPLYADATE).toLocaleString() : '-';
        pur_or_red.REDEEMADATE = popUpData.pur_or_red2[0].REDEEMADATE ? new Date(popUpData.pur_or_red2[0].REDEEMADATE).toLocaleString() : '-';
        pur_or_red.RSUMPAYDATE = popUpData.pur_or_red2[0].RSUMPAYDATE ? new Date(popUpData.pur_or_red2[0].RSUMPAYDATE).toLocaleString() : '-';

        pur_or_red.OFORCB = popUpData.OFORCB == '1' ? '' : '';
      }
      
      return {
        // SECURITYCODE: popUpData.fund_info.SECURITYCODE,
        // SECURITYSNAME: popUpData.fund_info.SECURITYSNAME,
        OFORCB: pur_or_red.OFORCB,
        ENDDATE: pur_or_red.ENDDATE,
        PURTYPE: pur_or_red.PURTYPE,
        REDTYPE: pur_or_red.REDTYPE,
        NVPER: pur_or_red.NVPER,
        ANVPER: pur_or_red.ANVPER,
        UPDATEDATE: pur_or_red.UPDATEDATE,
        ASDATEOTC: pur_or_red.ASDATEOTC,
        RSDATEOTC: pur_or_red.RSDATEOTC,
        ASDATE: pur_or_red.ASDATE,
        RSDATE: pur_or_red.RSDATE,
        APPLYADATE: pur_or_red.APPLYADATE,
        REDEEMADATE: pur_or_red.REDEEMADATE,
        RSUMPAYDATE: pur_or_red.RSUMPAYDATE
      }
    }
  }
}
</script>

<style lang="less" scoped>
.pur_red_type_box{
  margin: 70px 0 0 100px;
  >div{
    line-height: 30px;
  }
}
</style>



