<template>
  <div>
    <div class="messageBox">
      <ul class="clearFloat">
        <li v-for="(item,index) of prop" 
            :key="index" class="messageLists clearFloat">
            <div class="messageTime">{{item.time}}</div>
            <div class="messageRight">
              <div @click="newsDetails(item)" class="title">{{item.title}}</div>
              <div class="content">{{item.content}}</div>
            </div>
        </li>
      </ul>
    </div>
    <div v-if="isShowDetails" class="detailsPopUp">
      <div class="detailsBox">
        <div>
          <h1>{{detailsData.news_title}}</h1>
          <h3>{{detailsData.sub_title}}</h3>
          <p>{{detailsData.news_date}}  {{detailsData.neeq_type}}</p>
          <div>
            {{detailsData.contenews_contentnt}}
          </div>
        </div>
        <span @click="closeDetails" class="closeBtn"></span>
      </div>
    </div>
  </div>
</template>

<script>
//       {
// code: "0",
// msg: "query success",
// result: {
// neeq_news_detail: {
// appendix: "('/uploads/1/file/public/201805/20180515165415_hy2bwpbp0l.docx')",
// click_num: null,
// neeq_id: 12,
// neeq_type: "",
// news_content: "('20184143020184','10197','31','9031.96%338.14%3780-9038.14%337.11%2370-8023.71%316.49%6706.19%38.25%','','6152.54035.71%3159.05%12.523.81%611.43%','540100%369.75%5-2000%30%2000%330.25%','','','1','18','2710005','37','','5BPM010-63889557',' ',' ',' ',' ','','2018','515')",
// news_date: "2018-05-15",
// news_title: "20184",
// sub_title: "2018"
// }
// }
// }
export default {
  props: ['prop'],
  data(){
    return {
      isShowDetails: false,
      detailsData: {}
    }
  },
  methods:{
    newsDetails(item){
      this.isShowDetails = false;
      this.$_axios.get('http://10.25.24.51:10189/api/risk/neeq_news_detail?neeq_id=' + item.neeq_id)
      .then(response => {
        this.isShowDetails = true;
        const resultData = response.data.result;
        this.detailsData = JSON.parse(JSON.stringify(resultData.neeq_news_detail));
        console.log(resultData)
      })
      .catch(err => {
        console.log(err);
      });
    },
    closeDetails(){
      this.isShowDetails = false;
      this.detailsData = {}
    }
  }
}
</script>

<style lang="less" scoped>
.messageBox{
  width: 100%;
  // background-color: darkgrey;
  .messageLists{
    float: left;
    width: 100%;
    height: 60px;
    box-sizing: border-box;
    margin-top: 30px;
    >div{
      float: left;
    }
  }
  .messageTime{
    box-sizing: border-box;
    width: 65px;
    height: 50px;
    background-color: #93b1b9;
    color: #fff;
    padding-left: 10px;
    padding-top: 5px;
    font-size: 21px;
    line-height: 19px;
  }
  .messageRight{
    width: 75%;
    margin-left: 15px;
    >div{
      height: 25px;
      line-height: 25px; 
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
    }
    .title{
      cursor: pointer;
    }
  }
}
.detailsPopUp{
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  border: 1px solid #797979;
  word-break:break-all;
  word-wrap:break-word;
  background-color: #fff;
  .detailsBox{
    position: relative;
    width: 900px;
    margin-left: auto;
    margin-right: auto;
    >div{
      height: 900px;
      overflow: hidden;
      >div{
        height: 823px;
        overflow-y: scroll;
      }
    }
    h1,h3{
      text-align: center;
      line-height: 40px;
    }
    p{
      text-align: center;
      line-height: 30px;
    }
    .closeBtn{
      position: absolute;
      right: -50px;
      top: 30px;
      font-size: 40px;
      cursor: pointer;
    }
  }
}
</style>



