<template>
    <ul class="mo-paging">
        <!-- prev -->
        <li
        :class="['paging-item', 'paging-item--prev', {'paging-item--disabled' : index === 1}]"
        @click="prev">prev</li>
        
        <!-- first -->
        <li
        :class="['paging-item', 'paging-item--first', {'paging-item--disabled' : index === 1}]"
        @click="first">first</li>
        
        <li
        :class="['paging-item', 'paging-item--more']"
        v-if="showPrevMore">...</li>

        <li
          :class="['paging-item', {'paging-item--current' : index === pager}]"
          v-for="(pager, index) in pagers" :key="index"
          @click="go(pager)">{{ pager }}</li>

        <li
        :class="['paging-item', 'paging-item--more']"
        v-if="showNextMore">...</li>
        
        <!-- last -->
        <li
        :class="['paging-item', 'paging-item--last', {'paging-item--disabled' : index === pages}]"
        @click="last">last</li>

        <!-- next -->
        <li
        :class="['paging-item', 'paging-item--next', {'paging-item--disabled' : index === pages}]"
        @click="next">next</li>
    </ul>
</template>
<script>
export default {
    name : 'MoPaging',
    //props
    props : {

        //..., 
        perPages : { 
            type : Number,
            default : 5 
        },

        //
        pageIndex : {
            type : Number,
            default : 1
        },

        //
        pageSize : {
            type : Number,
            default : 10
        },

        //
        total : {
            type : Number,
            default : 1
        },

    },
    methods : {
        prev(){
            if (this.index > 1) {
                this.go(this.index - 1)
            }
        },
        next(){
            if (this.index < this.pages) {
                this.go(this.index + 1)
            }
        },
        first(){
            if (this.index !== 1) {
                this.go(1)
            }
        },
        last(){
            if (this.index != this.pages) {
                this.go(this.pages)
            }
        },
        go (page) {
            if (this.index !== page) {
                this.index = page
                //change
                this.$emit('change', this.index)
            }
        }
    },
    computed : {

        //
        pages(){
            return Math.ceil(this.size / this.limit)
        },

        //count
        pagers () {
            const array = []
            const perPages = this.perPages
            const pageCount = this.pages
            let current = this.index
            const _offset = (perPages - 1) / 2

            
            const offset = {
                start : current - _offset,
                end   : current + _offset
            }

            //-1, 3
            if (offset.start < 1) {
                offset.end = offset.end + (1 - offset.start)
                offset.start = 1
            }
            if (offset.end > pageCount) {
                offset.start = offset.start - (offset.end - pageCount)
                offset.end = pageCount
            }
            if (offset.start < 1) offset.start = 1

            this.showPrevMore = (offset.start > 1)
            this.showNextMore = (offset.end < pageCount)

            for (let i = offset.start; i <= offset.end; i++) {
                array.push(i)
            }

            return array
        }
    },
    data () {
        return {
            index : this.pageIndex, //
            limit : this.pageSize, //
            size : this.total || 1, //
            showPrevMore : false,
            showNextMore : false
        }
    },
    watch : {
        pageIndex(val) {
            this.index = val || 1
        },
        pageSize(val) {
            this.limit = val || 10
        },
        total(val) {
            this.size = val || 1
        }
    }
}
</script>
<style lang="sass" scoped>
.mo-paging 
    display: inline-block
    padding: 0
    margin: 1rem 0
    font-size: 0
    list-style: none
    user-select: none
    > .paging-item 
        display: inline
        font-size: 14px
        position: relative
        padding: 6px 12px
        line-height: 1.42857143
        text-decoration: none
        border: 1px solid #ccc
        background-color: #fff
        margin-left: -1px
        cursor: pointer
        color: #0275d8
        &:first-child 
            margin-left: 0
        
        &:hover 
            background-color: #f0f0f0
            color: #0275d8
        
        &.paging-item--disabled,
        &.paging-item--more
            background-color: #fff
            color: #505050
        
        //
        &.paging-item--disabled 
            cursor: not-allowed
            opacity: .75
        
        &.paging-item--more,
        &.paging-item--current 
            cursor: default
        
        //
        &.paging-item--current 
            background-color: #0275d8
            color:#fff
            position: relative
            z-index: 1
            border-color: #0275d8
        
    

</style>




