<template>
    <div>
        <div class="homePageMain clearFloat">
            <sidebar :prop="sidebarData"></sidebar>
            <div v-if="routerGoNow" class="homePageChildPage clearFloat">
                <router-view></router-view>
            </div>
        </div>
    </div>
</template>
<script>
import sidebar from './sidebar';
import homeContent from './homeContent';
export default {
    data(){
        return {
            isShowList: false,
            sidebarData: [
                {title: '', isShowList: false, list: [
                    {title: '', path: '/homePage/stock_newsWarning', list: []},
                    {title: '', path: '/homePage/stock_afficheWarning', list: []},
                    {title: '', path: '/homePage/stock_financeWarning', list: []},
                    {title: '', path: '/homePage/stock_sharePriceWarning', list: []}
                ]},

                {title: '', isShowList: false, list: [
                    {title: '', isShowList: false, path: '/homePage/homeContent', list: [
                        {title: '', path: '/homePage/bond_negativeNews'},
                        {title: '', path: '/homePage/bond_newsFavorite'}
                    ]},
                    {title: '', isShowList: false, path: '/homePage/homeContent', list: [
                        {title: '', path: '/homePage/bond_issuerNotice'},
                        {title: '', wordWrapping: true, path: '/homePage/bond_manageNotice'}
                    ]},
                    {title: '', isShowList: false, path: '/homePage/homeContent', list: [
                        {title: '', path: '/homePage/bond_ratingInformation'},
                        {title: '', path: '/homePage/bond_ratingChange'}
                    ]}
                ]},

                {title: '', isShowList: false, list: [
                    {title: '', isShowList: false, path: '/homePage/homeContent', list: [
                        {title: '', path: '/homePage/fund_shigekuraNews'},
                        {title: '', path: '/homePage/fund_managerNews'},
                        {title: '', path: '/homePage/fund_newsFavorite'}
                    ]},
                    {title: '', path: '/homePage/fund_issuerNotice', list: []},
                    {title: '', path: '/homePage/fund_info', list: []}
                ]},
                
                {title: '', isShowList: false, list: [
                    {title: '', path: '/homePage/stock_index_futures', list: []}
                ]},

                {title: '', isShowList: false, list: [
                    {title: '', wordWrapping: true, path: '/homePage/shareOption_page1', list: []},
                    {title: '', path: '/homePage/shareOption_page2', list: []}
                ]},

                {title: '', isShowList: false, list: [
                    {title: '', isShowList: false, path: '/homePage/NEEQ_NewsMonitoring', list: [
                        {title: '', path: '/homePage/NEEQ_NewsMonitoring'},
                        {title: '', path: '/homePage/NEEQ_favorite'}
                    ]},
                    {title: '', isShowList: false, path: '/homePage/NEEQ_announcement', list: [
                        {title: '', path: '/homePage/NEEQ_announcement'},
                        {title: '', path: '/homePage/NEEQ_dynamic'}
                    ]},
                    {title: '', isShowList: false, path: '/homePage/NEEQ_selfMeasures', list: [
                        {title: '', path: '/homePage/NEEQ_selfMeasures'},
                        {title: '', path: '/homePage/NEEQ_punishment'},
                        {title: '', path: '/homePage/NEEQ_inquiryLetter'},
                    ]}
                ]},

                {title: '', isShowList: false, list: [
                    {title: '', path: '/homePage/supervise_page1', list: []},
                    {title: '', wordWrapping: true, path: '/homePage/supervise_page2', list: []},
                    {title: '', path: '/homePage/supervise_page3', list: []}
                ]},

                {title: '', path: '/homePage/legislation', isShowList: false, lastChild: true, list: []},
            ]
        }
    },
    computed:{
        routerGoNow(){
           return this.$store.state.routerGoNow;
        }
    },
    components:{
        sidebar,
        homeContent
    },
    methods:{
        showList(){
            this.isShowList = !this.isShowList;
        }
    }
}
</script>
<style lang="less" scoped>

.homePageMain{
    width: 1719px;
}
.homePageChildPage{
    // background-color: #99FFFF;
    float: left;
    width: 1180px;
    margin-left: 65px;
    margin-top: 50px;
}
</style>




